.iframe-lightbox {
  opacity: 0;
  z-index: 999999;
  transition: opacity .2s;
  display: none;
  position: fixed;
}

.iframe-lightbox .backdrop {
  cursor: default;
  background-color: rgba(0, 0, 0, .92);
  position: absolute;
}

.iframe-lightbox .content-holder {
  width: 80%;
  z-index: 1000;
  margin-right: -50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.iframe-lightbox .content {
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
}

.iframe-lightbox .content > .body {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.iframe-lightbox .content > .body .half-circle-spinner, .iframe-lightbox .content > .body .half-circle-spinner * {
  box-sizing: border-box;
}

.iframe-lightbox .content > .body .half-circle-spinner {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  margin: 0 -50% 0 0;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.iframe-lightbox .content > .body .half-circle-spinner .circle {
  content: "";
  width: 100%;
  height: 100%;
  border: 6px solid rgba(0, 0, 0, 0);
  border-radius: 100%;
  position: absolute;
}

.iframe-lightbox .content > .body .half-circle-spinner .circle.circle-1 {
  border-top-color: #fff;
  animation: half-circle-spinner-animation 1s infinite;
}

.iframe-lightbox .content > .body .half-circle-spinner .circle.circle-2 {
  border-bottom-color: #fff;
  animation: half-circle-spinner-animation 1s infinite alternate;
}

@keyframes half-circle-spinner-animation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.iframe-lightbox .content > .body.is-loaded .half-circle-spinner {
  display: none;
}

.iframe-lightbox iframe {
  width: 100%;
  height: 100%;
  border: 0;
  transition: opacity .2s;
  display: block;
  box-shadow: .267rem .267rem .267rem rgba(3, 3, 3, .3);
}

.iframe-lightbox.is-showing {
  display: block;
}

.iframe-lightbox.is-opened {
  opacity: 1;
}

.iframe-lightbox .btn-close {
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  outline: none;
  font-size: 1rem;
  display: block;
  position: fixed;
  top: 1rem;
  right: 1rem;
}

.iframe-lightbox .btn-close:before, .iframe-lightbox .btn-close:after {
  content: " ";
  width: .125em;
  height: 1.5em;
  background-color: #fff;
  position: absolute;
  top: 0;
  right: .688em;
}

.iframe-lightbox .btn-close:before {
  transform: rotate(45deg);
}

.iframe-lightbox .btn-close:after {
  transform: rotate(-45deg);
}

.iframe-lightbox--open {
  -webkit-overflow-scrolling: touch;
  -ms-touch-action: auto;
  touch-action: auto;
  height: auto;
  overflow: hidden;
}

